.hide {
    visibility: hidden;
}

.hide2 {
    display: none;
}

hr {
    background-color: #091e4221;
    border: 0;
    height: 1px;
    padding: 0;
    width: 100%;
}

.secondary-header {
    display: flex;
    height: 44px;
    background-color: rgba(0, 0, 0, 0.1);
    padding: 6px 4px;
    justify-content: space-between;
    align-items: center;

    .logo {
        width: 80px;
        height: 100%;
        display: flex;
        align-items: center;
        font-size: 20px;
        font-weight: 700;
        margin: 0 12px;

        span {
            color: white;
            @include flex-center;
        }

        img {
            width: 30px;
            height: 30px;
            object-fit: contain;

            @include flex-center;
        }
    }

    .button-top {
        position: relative;

        span {
            padding-right: 3px;
        }
    }

    div {
        height: 100%;
        align-items: center;
    }

    h1 {
        padding-right: 10px;
    }
}

.header-search-div {
    box-sizing: border-box;
    border: 1px solid rgba(255, 255, 255, 0.292);
    background-color: rgba(255, 255, 255, 0.235);
    color: white;
    display: flex;
    border-radius: 3px;
    width: 250px;

    &:hover {
        background-color: rgba(255, 255, 255, 0.3);
    }

    ::placeholder {
        color: white;
    }



    .label-search {
        justify-content: space-between;
        align-items: center;
        padding-left: 8px;
        flex: 1;
        font-size: 14px;


        &:focus-within {
            background-color: white;
            color: $clr-text-group;
        }

        .header-search {
            &:focus {
                background-color: #ffffff;
                outline: none;

            }

            border: none;
            background: transparent;
            height: 32px;
            width: 100%;
        }

        .fa-magnifying-glass {
            @include flex-center;
            padding-right: 4px;
        }
    }
}

.nav-header {
    .secondary-header-button {
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
            "Helvetica Neue", sans-serif;
        font-size: 14px;
        position: relative;
        background-color: transparent;
        height: 100%;
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
        padding: 0 12px;
        border-radius: 3px;

        &.create {
            background-color: rgba(255, 255, 255, 0.169);
        }

        &:hover {
            background-color: rgba(255, 255, 255, 0.3);
        }

        &:active {
            background-color: rgba(255, 255, 255, 0.4);
        }

    }

    .header-btn {
        align-items: center;
        display: flex;
        height: 100%;
        position: relative;
        gap: 10px;

        >* {
            >* {
                color: white;
                font-size: 14px;
            }
        }
    }

    align-items: center;
    height: 100%;
}

.header-modal {
    position: absolute;
    width: 304px;
    will-change: top, left;
    top: 45px;
    left: 0;
    background-color: #ffffff;
    border-radius: 3px;
    box-shadow: 0 8px 16px -4px rgb(9 30 66 / 25%), 0 0 0 1px rgb(9 30 66 / 8%);
    z-index: 20;
    border: 1px solid rgba(147, 147, 147, 0.525);
    padding: 0px 12px 12px 12px;
    transition: 0.3s;
    overflow-x: hidden;
    overflow-y: auto;

    hr {
        margin-top: 0;
        margin-bottom: 10px;
    }

    ul {
        width: 100%;
        padding: 0px;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        gap: 10px;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
            "Helvetica Neue", sans-serif;
        color: #172b4d;
        font-weight: 600;

        li {
            .header-star-template {
                line-height: 18px;
                width: 100%;
                display: flex;
                gap: 8px;
                font-size: 14px;
            }

            span {
                font-size: 14px;
            }

            .template-indactor {
                justify-self: flex-end;
                align-self: center;
                border-radius: 3px;
                font-size: 11px;
                font-weight: bold;
                line-height: 16px;
                color: $clr-text-group;
                padding: 3px 8px;
            }
        }
    }

    li {
        width: 100%;
        display: flex;
        align-items: center;
        gap: 6px;
        cursor: pointer;

        .template-img {
            height: 32px;
            width: 40px;
            border-radius: 3px;
        }

        &:hover li {
            background-color: #5e6c84;
        }
    }

    .modal-title {
        display: flex;
        height: 40px;
        width: 100%;
        justify-content: center;
        color: #5e6c84;
        font-size: 14px;
        line-height: 16px;
        margin-top: 16px;
        margin: 3px 12px;

        h1 {
            font-weight: 400;
        }

        button {
            background-color: transparent;
            width: 40px;
            height: 40px;
            padding: 12px;
            position: absolute;
            right: 0;
            color: #6b778c;
            font-size: 12px;
        }
    }
}


.user-logo {
    aspect-ratio: auto 32/32;
    border-radius: 25em;
    height: 32px;
    width: 32px;
    padding-right: 1px;
    padding-bottom: 3px;
    @include flex-center;
    color: white;
}

.template-indactor {
    background-color: rgba(9, 30, 66, 0.13);
    justify-self: flex-end;
}

.header-finish {
    .button-top {
        border-radius: 3px;
        border: 0;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        box-shadow: none;
        font-weight: bold;
        height: 32px;
        width: 28px;
        line-height: 32px;
        padding: 0;
        white-space: nowrap;
        color: #ffffff;
        position: relative;

        &:hover {
            background-color: rgba(255, 255, 255, 0.3);
        }

        &:active {
            background-color: rgba(255, 255, 255, 0.4);
        }
    }

    .button-header-finish {

        button {
            width: 32px;
            height: 32px;
            font-size: 16px;
        }

        label {
            width: 32px;
            height: 32px;
        }
    }

    .search-offer {
        align-self: flex-start;
        color: #5e6c84;
        font-size: 12px;
        line-height: 16px;
        margin-top: 16px;
        font-weight: 600;
        margin: 3px 12px;
    }

    .header-search-div {
        position: relative;
        display: flex;
        margin-right: 4px;

        .header-modal {
            top: 45px;
            left: 0;
            overflow-x: hidden;
            overflow-y: auto;
            display: flex;
            gap: 3px;

            .template-img {
                height: 16px;
                width: 20px;
                border-radius: 3px;
                margin-right: 3px;
            }

            li {
                cursor: pointer;
                text-align: center;

                .header-search-star {
                    opacity: 0;
                    transition: 0.3s;
                    cursor: pointer;
                }

                &:hover .header-search-star {
                    opacity: 1;
                }

                span {
                    &:hover i {
                        color: yellow;
                        transition: 0.3s;
                    }
                }
            }

            .template-indactor {
                border-radius: 3px;
                font-size: 11px;
                padding: 3px 8px;
                font-weight: bold;
                line-height: 16px;
                align-items: center;
                align-self: flex-end;
                color: $clr-text-group;
                margin-left: 3px;
            }
        }
    }

    .info-modal {
        position: absolute;
        width: 380px;
        height: 384px;
        top: 48px;
        left: -352px;
        overflow-y: hidden;

        .info-img-div {
            display: flex;
            flex-direction: column;
            padding: 0 12px;

            img {
                border-radius: 3px;
                width: 336px;
                height: 168px;
            }

            .for-h3 {
                height: 56px;
                padding: 8px;
                display: flex;
                flex-wrap: wrap;
                white-space: nowrap;

                h3 {
                    white-space: normal;
                    width: 100%;
                    font-size: 16px;
                    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
                        "Open Sans", "Helvetica Neue", sans-serif;
                    color: $clr-text-group;
                }
            }

            hr {
                background-color: #091e4221;
                margin: 8px 0;
                border: 0;
                height: 1px;
                padding: 0;
                width: 100%;
            }

            button {
                border-radius: 3px;
                font-size: 14px;
                color: #5e6c84;
                display: block;
                text-decoration: underline;
                font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
                    "Open Sans", "Helvetica Neue", sans-serif;
                transition-property: background-color, border-color, box-shadow;
                transition-duration: 85ms;
                transition-timing-function: ease;
                background-color: transparent;
                margin: 0 0 8px;
                padding: 6px 12px;
                height: 32px;

                &:hover {
                    background-color: rgba(9, 30, 66, 0.08);
                    color: $clr-text-group;
                }
            }

            .info-button-buttom {
                display: flex;
                height: 34px;
                justify-content: space-evenly;
                text-align: center;
                font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell,
                    "Open Sans", "Helvetica Neue", sans-serif;
                text-decoration: underline;
                color: #5e6c84;

                button {
                    margin: 0 1px 2px;
                    padding: 6px 8px;
                    width: 58px;
                    height: 32px;
                }
            }
        }
    }

    position: relative;
    gap: 4px;
}


.create-first {
    padding: 0;

    .create-first-actions {
        display: flex;
        width: 100%;
        flex-direction: column;
        display: inline-block;
        padding-bottom: 12px;

        button {
            width: 100%;
            padding: 0;
            margin: 0;
            padding: 6px 12px;
            color: $clr-text-group;
            background-color: transparent;
            font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

            &:hover {
                background-color: rgba(9, 30, 66, 0.04);
            }

            .action-title {
                display: inline-block;
                width: 100%;
                font-size: 16px;
                text-align: start;
            }

            .action-desc {
                padding-top: 4px;
                font-size: 12px;
                text-align: start;
            }
        }

    }
}

.on-go-back-button {
    position: absolute;
    left: 0;

}

.search {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    width: 350px;

    ul {
        li {
            .to-select-template {
                width: 100%;
                display: flex;
                gap: 8px;
                align-items: center;

                .template-indactor {
                    align-self: center;
                    justify-self: flex-end;
                }
            }

        }
    }
}



@include for-tablet-layout {

    .secondary-header {
        width: 100%;
        gap: 4px;

        .header-container {
            width: 100%;
            justify-content: space-between;

        }

        .nav-header {
            .button-top {
                .starred {
                    display: none;
                }

                .templates {
                    display: none;
                }

                .header-modal {
                    left: -250px;
                }
            }

            .create {
                border-radius: 50%;
                width: 32px;

                .create-for-responsive {
                    display: none;
                }

                .plus-for-responsive {
                    display: block;
                    padding: 0;
                }
            }
        }

        .header-finish {
            .header-search-div {
                display: none;
            }

            .button-top {
                background-color: rgba(255, 255, 255, 0.169);
                border-radius: 50%;
                width: 32px;

                .info-modal {
                    position: fixed;
                    left: 50%;
                    transform: translateX(-50%);
                    width: 350px;

                    .info-img-div {
                        img {
                            border-radius: 3px;
                            width: 300px;
                            height: 150px;
                        }
                    }
                }
            }

            .bell {
                display: none;
            }
        }
    }
}